:root {
    --theme-blue: #657ED4;
    --offwhite: #dddddd;
}

.box {
    width: 50%;
    height: 50%;
    background-color: red;
}

.header {
    background-color: rgba(0,0,0,.5);
    height: 6vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    backdrop-filter: blur(5px);
}
  
.header > a {
    text-decoration: none;
    color: var(--offwhite);
    font-size: 18px;
    font-weight: 700;
}

.header > a:hover {
    color: var(--theme-blue);
    cursor: pointer;
}

.cover {
    background-image: url("./images/bgimg.jpg");
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -6vh;
}
  
.container {
    color: white;
    text-align: center;
    background-color: rgba(0,0,0,.3);
    border-radius: 40px;
    padding: 20px;
    backdrop-filter: blur(5px);
    width: 50vw;
    height: 450px;
}

.summary > h2 {
    margin-top: 7.5vh;
    margin-bottom: 2.5vh;
    font-size: 48px;
}

.summary > p {
    padding: 2vh 2.5vw 1vh; 
}

.summary > div {
    display: flex;
    justify-content: center;
    gap: 20px;
}
  
.summary > div > a {
    background-color: rgba(255,255,255,0.0);
    text-decoration: none;
    display:flex;
    align-items: center;
    border-color: var(--offwhite);
    color: var(--offwhite);
    border-style: solid;
    border-radius: 80px;
    font-size: 18px;
    padding: 10px 25px;
    margin-top: 10px;
}

.summary > div > a > img {
    width: 1.5em;
    margin-left: 10px;
}

.summary > div > a:hover {
    color: var(--theme-blue);
    border-color: var(--theme-blue);
}

.resume-container {
    display:flex;
    margin: 2% 0 0 0;
    gap: 12%;
    align-items: center;
    justify-content: center;
} 

.resume-header-buttons {
    text-decoration: none;
    color: var(--offwhite);
    font-size: 18px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    gap: 35px;
}

.resume-header-buttons > a {
    padding: 5px;
}

.resume-header-buttons > a:focus {
    color: var(--theme-blue);
    cursor: pointer;
}
  
.resume-header-buttons > a:hover {
    color: var(--theme-blue);
    cursor: pointer;

}

.education-info > p > a {
    color: var(--offwhite);
    text-decoration: none;
}

.education-info > p > a:hover {
    color: var(--theme-blue);
    text-decoration: underline;
}

.education-info > p > a + a{
    margin-left: 6px;
}

.education-info > p:nth-of-type(odd) {
    margin: -20px 0 0 0;
    font-size: 12px;
}

.education-info > p:nth-of-type(even) {
    margin: 40px 0 0 0;
}

.experience-buttons > button{
    border: none;
    background: none;
    color: white;
    text-decoration: underline;
    padding: 10px;
}

.experience-buttons > button:hover, button:focus {
    background: rgba(0,0,0,.3);
    color: var(--theme-blue);
    border-radius: 10px;;
    padding: 10px;

}

.work-info > h2 {
    font-size: 2rem;
}

.work-info > p:nth-child(2) {
    margin-top: -20px;
    font-size: 12px;
}

.work-info > ul {
    margin: 40px 40px 0 40px;
}

.skill-info {
    width: 60%;
    /* background-color: #657ED4; */
}

.skill-info > div > h2{
    margin: 10px 0px -5px;
}

.skill-bars {
    display: grid;
    grid-auto-flow: column;
    grid-template: 0.25fr 0.5fr / 2fr 2fr 2fr;
    column-gap: 20px;
}

.skill-container {
    box-sizing:border-box;
    width: 100%;
    background: #ddd;
    border-radius: 20px;
}

.bar {
    text-align: right;
    padding: 5px 0;
    color: white;
    border-radius: 20px;
}

.react-skill {
    width: 60%;
    background-color: #61DBFB;
    padding-right: 10px;
}

.css-skill {
    width: 80%;
    background-color: #D81E5B;
    padding-right: 10px;
}

.js-skill {
    width: 80%;
    background-color: #D64933;
    padding-right: 10px;
}

.wordpress-skill {
    width: 50%;
    background-color: #3858E9;
    padding-right: 10px;
}

.elementor-skill {
    width: 90%;
    background-color: #FFC5F3;
    padding-right: 10px;
}

.divi-skill {
    width: 70%;
    background-color: #9836EB;
    padding-right: 10px;
}

.powershell-skill {
    width: 70%;
    background-color: #002353;
    padding-right: 10px;
}

.python-skill {
    width: 60%;
    background: linear-gradient(90deg, rgba(55,108,153,1) 0%, rgba(246,208,73,1) 100%);
    padding-right: 10px;
}

.zapier-skill {
    width: 90%;
    background-color: #FF4F00;
    padding-right: 10px;
}

 .portfolio-container {
    display: flex;
    margin-top: 10%;
    justify-content: center;
} 

.portfolio-container > button {
    background: none;
    border: none;
    max-width: 300px;
    color: white;
}

.portfolio-container > button:hover {
    background-color: rgba(0,0,0,.3);
    border-radius: 20px;
}

.portfolio-container > button > img {
    max-width: 100%;
    border-radius: 10px;
    padding-top: 10px;
}

.portfolio-container > button:nth-child(3) > img {
    max-width: 95%;
}

.channelOne {
    margin-left: -150px;
    margin-top: 50px;
    display: grid;
    grid-template-columns: 2fr 1fr;

}

.channelOne > div {
    margin-left: -150px;
}

.channelOne > a > img {
    max-width: 50%;
}

.jwi {
    margin-left: -150px;
    margin-top: 50px;
    display: grid;
    grid-template-columns: 2fr 1fr;

}

.jwi > div {
    margin-left: -150px;
}

.jwi > a > img {
    max-width: 50%;
}

.mnig {
    margin-left: -150px;
    margin-top: 50px;
    display: grid;
    grid-template-columns: 2fr 1fr;

}

#mnig-images {
    max-width: 80%;
    display: grid;
    margin-left: 70px;
    margin-top: -30px;

}

.mnig > div {
    margin-left: -150px;
} 

.mnig a > img {
    max-width: 50%;
} 

.portfolio-button {
   background: none;
    color: white;
    border: none;
    font-size: 24px;
    padding: 10px;
}

.portfolio-button:hover {
    background: rgba(0,0,0,.3);
    border-radius: 20px;
    padding: 10px;
}

.fade-in {
    animation: fade-in-anim 0.5s ease-out;
}

.fade-out {
    animation: fade-out-anim 0.5s ease-out;
    animation-fill-mode: both;
}

.animate-skill {
    animation: animate-skill 1s ease-out;
    animation-fill-mode: both;
}

@keyframes fade-in-anim {
    from {opacity: 0;}
    to {opacity: 100;}
}

@keyframes fade-out-anim {
    from {opacity: 100;}
    to {opacity: 0;}
}

@keyframes animate-skill {
    from {width: 0;}
}