body {

    #contact-form {
        // position: absolute;
        color: #FFF;
        font-family: sans-serif;
        // padding: 20px;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
        margin: 7.5vh 25%;

        input,
        textarea {
            width: 100%;
            border: 0;
            padding: 10px 20px;
            box-sizing: border-box;
            margin-bottom: 15px;
            border-radius: 20px;
        }

        textarea {
            height: 100px;
            resize: vertical;
            border-radius: 20px;
            resize: none;
        }

        button {
            width: 100%;
            padding: 10px;
            border: 0;
            cursor: pointer;
            background: #657ED4;
            font-weight: bold;
            color: #FFF;
            font-size: 18px;
            margin-bottom: 15px;
            text-transform: uppercase;
            border-radius: 20px;
        }

        span {
            display: none;
            text-align: center;

            &.visible {
                display: block;
            }
        }
    }
}